<template>
  <van-dialog :show="show"
              :title="this.compParams.title"
              :show-cancel-button="this.compParams.showCancelButton"
              :show-confirm-button="this.compParams.showConfirmButton"
              :theme="this.compParams.theme"
              :overlay="this.compParams.overlay"
              :close-on-popstate="this.compParams.closeOnPopstate"
              :close-on-click-overlay="this.compParams.closeOnOverlay"
              @close="close()">
    <div class="ver">
      <div class="ver-title">
        
      </div>
    </div>
  </van-dialog>
</template>

<script>
import { Dialog } from 'vant'

export default {
  name: 'UserListMenuDeve',
  emits: ['close-deve'],
  props: {
    show: {
      type: Boolean,
      require: true,
      default: false,
      validator (value) {
        return [true, false].includes(value)
      }
    }
  },
  components: {
    'van-dialog': Dialog.Component
  },
  data () {
    return {
      compParams: {
        title: '', // Dialog
        width: '320px', // px320
        message: '', //  \n 
        messageAlign: '', // left center right
        theme: 'default', //  round-button default
        showConfirmButton: false, // true
        confirmButtonText: '', // 
        confirmButtonColor: '#ee0a24', // #ee0a24
        showCancelButton: false, // true
        cancelButtonText: '', // 
        cancelButtonColor: 'black', // black
        overlay: true, // true
        closeOnPopstate: true, // true
        closeOnOverlay: true, // false
        lockScroll: true, // false
        allowHtml: true //  message  HTMLfalse
      }
    }
  },
  methods: {
    close () {
      this.$emit('close-deve')
    }
  }
}
</script>

<style scoped>
.ver {
  font-size: 1rem;
  text-align: center;
  margin: 1.5rem 1rem 1rem 1.5rem;
}
.ver-title {
  font-size: 5rem;
  color: red;
  margin-top: 5rem;
  margin-bottom: 5rem;
}
</style>
