<template>
  <van-notice-bar
      :left-icon="leftIcon"
      :scrollable="scroll"
      :mode="mode"
      :deploy="delay"
      :speed="speed"
      :color="color"
      :background="background"
      :text="text"
      @click="click()"
     />
</template>

<script>
import { NoticeBar } from 'vant'
import router from '../../router/index.ts'

export default {
  name: 'HeroListShowDetailNotice',
  components: {
    'van-notice-bar': NoticeBar
  },
  data () {
    return {
      leftIcon: 'volume-o', // 
      text: 'bloghttp://pygo2.top/', // 
      scroll: true, // 
      wrap: false, // 
      mode: 'link', // closeable:  link: 
      color: '#1989fa', // 
      background: '#ecf9ff', // 
      delay: 1, //  (s)
      speed: 60, //  (px/s)
      url: 'http://pygo2.top/'
    }
  },
  watch: {
    mode (newVal, oldVal) {
      if (newVal === 'link') {
        router.push(this.url)
      }
    }
  },
  methods: {
    close () {
      console.log('close notice')
    },
    click () {
      if (this.url.indexOf('http://') > -1) {
        window.location.href = this.url
      } else if (this.url.indexOf('https://') > -1) {
        window.location.href = this.url
      } else {
        window.location.href = 'http://' + this.url
      }
    }
  }
}
</script>

