<template>
  <van-swipe :initial-swipe="this.swipeParams.index"
             :autoplay="this.swipeParams.autoplay"
             :loop="this.swipeParams.loop"
             :show-indicators="this.swipeParams.indicators"
             :indicator-color="this.swipeParams.indicatorColor"
             :lazy-render="this.swipeParams.lazy"
             :touchable="this.swipeParams.touchable"
             :duration="this.swipeParams.duration"
             @change="onChange">
    <van-swipe-item v-for="(item, index) in imageList" :key="index">
      <img class="user-poster" :src="item.url" />
    </van-swipe-item>
  </van-swipe>
  <van-notify v-model:show="this.notParams.show"
              :color="this.notParams.color"
              :type="this.imageList[this.current_image_index].type">
    <van-icon :name="this.imageList[this.current_image_index].icon"
              class="not-icon" />
    <span> {{ this.imageList[this.current_image_index].desc }}</span>
  </van-notify>
</template>

<script>
import { Swipe, SwipeItem, Notify, Icon } from 'vant'

export default {
  name: 'UserListSwipe',
  components: {
    'van-swipe': Swipe,
    'van-swipe-item': SwipeItem,
    'van-notify': Notify.Component,
    'van-icon': Icon
  },
  data () {
    return {
      current_image_index: 0,
      swipeParams: {
        autoplay: 2400, //  ms
        duration: 800, //  ms500
        index: 0, // 0
        loop: true, // true
        indicators: true, // true
        indicatorColor: 'red', // #1989fa
        touchable: true, // true
        lazy: true
      },
      notParams: {
        show: true,
        duration: 1200, //  ms
        color: 'white', // 
        background: 'black' // 
      },
      imageList: [
        { name: 'python', url: 'http://pygo2.top/images/article_python_system.jpeg', icon: 'bell', type: 'primary', desc: 'Pyhton' },
        { name: 'vue', url: 'http://pygo2.top/images/article_vue.jpeg', icon: 'hot', type: 'success', desc: 'VUE' },
        { name: 'linux', url: 'http://pygo2.top/images/article_linux_yun.jpg', icon: 'good-job', type: 'danger', desc: 'Linux' },
        { name: 'postgresql', url: 'http://pygo2.top/images/postgresql.jpg', icon: 'youzan-shield', type: 'warning', desc: 'Postgresql' },
        { name: 'nginx', url: 'http://pygo2.top/images/article_nginx.jpg', icon: 'lock', type: 'success', desc: 'Nginx' },
        { name: 'github', url: 'http://pygo2.top/images/article_github.jpg', icon: 'wap-home', type: 'danger', desc: 'Github' }
      ]
    }
  },
  methods: {
    onChange (index) {
      // swipe
      this.current_image_index = index
      // notify
      this.notParams.show = true
      setTimeout(() => {
        this.notParams.show = false
      }, this.notParams.duration)
    }
  }
}
</script>

<style scoped>
.user-poster {
  width: 100%;
  height: 53vw;
  display: block;
}
.not-icon {
  margin-right: 4px;
}
</style>
